@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?89223323');
  src: url('../font/fontello.eot?89223323#iefix') format('embedded-opentype'),
    url('../font/fontello.woff2?89223323') format('woff2'),
    url('../font/fontello.woff?89223323') format('woff'),
    url('../font/fontello.ttf?89223323') format('truetype'),
    url('../font/fontello.svg?89223323#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
